.container {
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: 100%;
  position: relative;
}
.nav {
  position: relative;
}
.nav .logo-u {
  position: absolute;
  left: 40px;
  top: 0;
}
.nav .logo-u .img {
  width: 300px;
}
.module-nav {
  width: 960px;
  margin: 0 auto;
}
.module-nav .nav-title {
  margin-bottom: 60px;
  text-align: center;
}
.module-nav .nav-body .list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
.module-nav .nav-body .list .item {
  width: 300px;
  border-radius: 4px;
  border: 1px solid #ebeef5;
  background-color: #fff;
  overflow: hidden;
  color: #303133;
  transition: 0.3s;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.module-nav .nav-body .list .item .item-link {
  display: block;
}
.module-nav .nav-body .list .item .item-link .img {
  display: block;
  width: 100%;
}
.module-nav .nav-body .list .item .item-info {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 12px 0;
  height: 80px;
  line-height: 1;
}
.module-nav .nav-body .list .item .item-info .info-row {
  text-align: center;
  font-size: 18px;
  color: #1652F0;
}
.module-nav .nav-body .list .item .item-info .info-row-tip {
  color: #303133;
}
.banner {
  position: relative;
  height: 800px;
  max-height: 800px;
  background: #fff url("../../images/university/top-bg.jpeg") center center no-repeat;
  background-size: 100% 800px;
}
.banner .banner-top-left {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-top: 800px solid rgba(255, 255, 255, 0.75);
  border-right: 1000px solid transparent;
}
.banner .banner-bottom-right {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 0;
  height: 0;
  border-bottom: 400px solid rgba(255, 255, 255, 0.75);
  border-left: 500px solid transparent;
}
.banner .banner-logo {
  position: absolute;
  left: 40px;
  top: 100px;
}
.content-box {
  position: relative;
}
.hero {
  margin: 240px 0 120px 0;
}
.about {
  position: relative;
  padding-bottom: 60px;
}
.about .heading {
  margin-top: 40px;
  margin-bottom: 40px;
  padding: 20px 0;
  text-align: center;
}
.about .para {
  max-width: 800px;
  margin: 0 auto;
}
.about .content {
  width: 80%;
}
.rect {
  width: 800px;
  min-height: 200px;
  margin: 0 auto;
  text-align: left;
  padding: 12px 40px 12px 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  background-color: #EEF1F5;
  box-shadow: 0px 5px 10px rgba(120, 130, 147, 0.4);
}
.rect .content {
  margin: 0 auto;
  vertical-align: middle;
}
.rect .heading {
  color: #1652f0;
  font-size: 22px;
  max-width: 530px;
  align-self: flex-start;
  line-height: 2;
}
.rect .para {
  text-align: left;
  max-width: 530px;
}
.rect {
  width: 624px;
  min-height: 230px;
  padding: 12px 30px 22px 30px;
}
.rect .heading {
  max-width: 600px;
}
.rect .para {
  max-width: 600px;
}
.find {
  height: 24rem;
  background-size: contain;
}
.hero {
  overflow-y: hidden;
}
.top-nav {
  position: fixed;
  top: 0;
  background-color: #FFFFFF;
  z-index: 100;
}
.hero-content {
  height: 100%;
}
.hero-content .left {
  display: flex;
  flex-direction: column;
  width: 40%;
  margin-left: 3.75rem;
  height: 80%;
  justify-content: center;
}
.hero-content .right {
  width: 60%;
  height: 85%;
  background: url(../../images/about/hero.png) no-repeat right center;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  position: absolute;
  top: 100px;
  right: 0;
  z-index: -2;
}
.title {
  font-family: "Noto Sans SC";
  font-weight: 400;
  color: #20242B;
  font-size: 3.125rem;
  line-height: 67px;
}
.title span {
  font-weight: 300;
  color: #1652F0;
  font-size: 1.25rem;
  line-height: 1rem;
}
.download {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 3.5rem;
  width: 100%;
}
.download-btn:first-child {
  margin-right: 1rem;
}
.info {
  margin: 100px auto;
  display: flex;
  justify-content: flex-start;
  position: relative;
  width: 1200px;
}
.info .txt-box {
  margin-bottom: 40px;
}
.info h3,
.info h4 {
  margin-bottom: 16px;
  font-size: 22px;
}
.info .txt {
  margin-bottom: 16px;
}
.menu-opt {
  font-family: "Noto Sans SC";
  font-size: 1.25rem;
  font-weight: 400;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1.25rem;
  color: #677382;
  margin-left: 3.75rem;
  margin-right: 3.75rem;
}
.menu-opt p {
  min-width: 230px;
}
.menu-opt img {
  width: 32px;
  height: 32px;
}
.menu-opt span {
  margin-right: 1rem;
}
.menu-opt:hover {
  background-color: #EEF1F5;
  border-radius: 3px;
  color: #1652F0;
  cursor: pointer;
}
.illustration-cloud {
  width: 45rem;
  height: 100%;
  background: url(../../images/about/cloud_right.jpg) no-repeat right center;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  position: absolute;
  z-index: -3;
  transform: translate(125%, 0);
}
.illustration-rocket {
  width: 25.5rem;
  height: 100%;
  background: url(../../images/about/rocket.png) no-repeat right top;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  position: absolute;
  top: 400px;
  left: 0;
  z-index: -1;
  transform: translate(-40%, 0);
}
@media only screen and (max-width: 414px) {
  .container {
    min-height: initial;
    min-width: initial;
    width: 100%;
    height: auto;
    padding-bottom: 160px;
  }
  .illustration-cloud {
    display: none;
  }
  .illustration-rocket {
    display: none;
  }
  .hero-content {
    padding: 68px 1rem 0;
  }
  .top-nav {
    position: relative;
  }
  .hero .hero-content .left {
    width: auto;
    height: auto;
    margin: 0 auto;
  }
  .hero .hero-content .left .title {
    padding: 0 0.125rem 0 0.125rem;
    line-height: 60px;
    text-align: center;
  }
  .hero .hero-content .left .strong {
    font-size: 24px;
  }
  .hero .hero-content .right {
    transform: translate(0, 30%);
    background-size: contain;
    width: 100%;
    height: 400px;
    background-position: top left;
  }
  .hero .download {
    margin-top: 2rem;
    align-items: center;
    justify-content: space-around;
  }
  .hero .download .download-btn img {
    height: 48px;
  }
  .info {
    width: auto;
    display: block;
  }
  .info .menu-opt {
    flex: 1;
    margin: 0;
    font-size: 14px;
    padding: 1rem 0.4rem;
    justify-content: center;
  }
  .info .menu-opt p {
    min-width: initial;
    text-align: center;
  }
  .info .ceo .profile {
    display: block;
  }
  .info .ceo .profile .profile-pic {
    width: 100%;
    text-align: center;
  }
  .info .ceo .profile .profile-info {
    width: 100%;
    margin-top: 12px;
  }
  .info .ceo .profile .profile-info .name {
    text-align: center;
  }
  .info .history .time {
    padding: 1rem 0;
  }
  .menu {
    display: flex;
    flex-basis: 30%;
    width: 100%;
    justify-content: space-around;
  }
  .menu .menu-opt span {
    display: none;
  }
  .text {
    margin-top: 2rem;
    padding: 0 1rem 0 1rem;
  }
  .time .event {
    flex-basis: 100%;
  }
}
